.works {
  // background-color: rgba(#FF6F61, 0.2)
  // margin: 227px auto
  padding: 227px 0 227px;
  overflow: hidden;
  &_recent {
 }    // min-height: 3000px
  &__title-wrapper {
    margin-bottom: 110px;
    @include sm {
      margin-bottom: 28px; } }
  &__title {
    position: relative;
    margin-bottom: 20px;
    @include sm {
      margin-bottom: 3px; }
    &_filter {
      span {
        display: inline-block !important; }
      &:after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        @include arr(10, 6, $red, t);
        transition: opacity 0.8s 1s, transform 0.8s;
        opacity: 0;
        .is-animate & {
          opacity: 1; }
        .is-open & {
          transform: rotate(-180deg); } } } }
  &__container {
    position: relative;
    opacity: 0;
    visibility: hidden;
    transition: opacity $tr, visibility $tr;
    margin: 0 -60px;
    @include lg {
      margin: 0 -50px; }
    @include sm {
      margin: 0 auto;
      padding-bottom: 40px; }
    &.is-init {
      opacity: 1;
      visibility: visible; } }
  &__item {
    width: 50%;
    padding-bottom: 107px;
    padding-left: 60px;
    padding-right: 60px;
    @include lg {
      padding-bottom: 111px;
      padding-left: 50px;
      padding-right: 50px; }
    @include sm {
      padding-bottom: 42px;
      padding-right: 0;
      padding-left: 0;
      width: 100%; } }
  &__filter-wrapper {
    margin-bottom: 82px; }
  &__filter {
    display: none;
    margin-top: 87px; }
  &__filter-btn {
    @extend .title;
    background-color: transparent; }
  &__btn {
    position: absolute;
    z-index: 5;
    width: 100%;
    left: 0;
    bottom: 0;
    // height: 30px
    // display: flex
    // align-items: center
    padding-left: 60px;
    @include lg {
      padding-left: 50px; }
    @include sm {
      padding-left: 0;
      bottom: 0; }
    .btn-to-left & {
      bottom: 117px;
      @include sm {
        bottom: 0; } }
    .anim-line {
      margin-right: 16px; } } }

.work {
  // width: 20%
  &__image-wrap {
    margin-bottom: 30px;
    overflow: hidden;
    transition: transform $tr;
    background-color: #ccc;
    backface-visibility: hidden;
    @include sm {
      margin-bottom: 20px; }
    @include hover {
      transform: scale(0.97);
      .work__image_scale {
        transform: scale(1.1); } } }
  &__image {
    transition: transform $tr;
    img {
      max-width: 100%;
      // width: 100%
      display: block; } }
  &__title {
    margin-bottom: 8px;
    @include sm {
      margin-bottom: 5px; } } }

.js-masonry-width {
  width: 50%;
  @include sm {
    width: 100%; } }

.js-masonry-stamp {
  position: absolute;
  right: 0;
  top: 0;
  height: 210px;
  width: 40%;
  @include lg {
    height: 165px; }
  @include sm {
    height: 0; } }


.filter-controls {
  display: flex;
  align-items: center;
  &__item {
    @include flex-size(20%); } }

