.header {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	padding-top: 61px;
	z-index: 100;
	&__inner {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		@include sm {
			align-items: flex-start; } }
	&__left {}
	&__menu {
		position: fixed;
		top: 0;
		left: 0;
		z-index: 2;
		width: 100%;
		height: 100vh;
		display: flex;
		align-items: center;
		opacity: 0;
		visibility: hidden;
		background-color: $white; }
	&__menu-top {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		padding-top: 61px;
		transform: translateY(-61px);
		opacity: 0; }
	&__wrap {
		width: 100%; }
	&__center {
		display: flex;
		align-items: center;
		justify-content: space-between; }
	&__text {}
	&__nav {}
	&__right {
		position: relative;
		padding-right: 26px;
		text-align: right;
		min-width: 50%; }
	&__btn {
		margin-right: 40px;
		@include sm {
			display: none; } }
	&__footer {} }
.logo {
	img {
		max-width: 100%;
		display: block; } }

.btn-menu {
	position: relative;
	z-index: 100;
	width: 26px;
	height: 40px;
	vertical-align: top;
	background-color: transparent;
	display: inline-block;
	.header & {
		position: fixed; }
	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: -7px;
		right: -7px;
		bottom: 0; }
	&__line {
		position: absolute;
		left: 0;
		width: 25px;
		height: 2px;
		transition: transform .3s;
		transform-origin: center;
		background-color: $dark;
		display: block;
		&:first-child {
			top: 14px;
			.btn-menu.is-open & {
				transform: translate(0,5px) rotate(45deg); } }
		&:last-child {
			top: 24px;
			.btn-menu.is-open & {
				transform: translate(0,-5px) rotate(-45deg); } } } }
.nav {
	&__list {}
	&__item {}
	&__link {} }
